*
  -webkit-box-sizing: border-box
  -moz-box-sizing: border-box
  box-sizing: border-box

html
  font-size: 62.5%

  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale

body
  margin: 0

::selection
  color: #fff
  background: $color-selection

::-webkit-scrollbar
  width: 5px

  background: #f6f6f6

::-webkit-scrollbar-thumb
  background-color: $scrollbar-color

// img reset
img
  max-width: 100%
  height: auto

// basic hr
hr,
.hr
  display: block

  height: 0.1rem
  margin: 0
  padding: 0

  border: 0
  background: $color-border

// wrapper
.wrapper
  max-width: 1000px
  margin: 0 auto
  padding: 30px

figure
  margin: 0

.mannenman
  height: 800px

.logo
  display: block

  height: 30px
  margin-bottom: 10px

body
  overflow: hidden

  max-width: 100vw
  max-height: 100vh

main
  display: flex
  align-items: center
  justify-content: center

  width: 100vw
  height: 100vh

.btn
  display: block

  padding: 11px 18px

  cursor: pointer
  transition: .3s ease all
  letter-spacing: 0.5px
  text-transform: uppercase

  color: rgba(#08153A, 0.9)
  border: none
  background: #D8DFEB

  font-family: "Avenir-Medium", Arial, Sans-Serif

  &:hover
    transform: scale(1.1)

.content
  .btn
    margin-bottom: 4.3rem

  h3
    margin-top: 2rem

.actions
  display: flex

.action
  display: flex
  align-items: center
  justify-content: center

  width: 3.1rem
  height: 3.1rem
  margin: 0
  margin-right: 10px
  padding: 0

  transition: .3s all ease

  color: $color-grey
  border: 1px solid $color-grey
  border-radius: 50%
  box-shadow: none

  font-family: "Avenir-Medium", Arial, Sans-Serif
  font-size: 1.4rem

  &:hover
    cursor: pointer
    transform: scale(1.1)

    color: #000
    background: #fff
    box-shadow: 0 1px 20px rgba(#9CA7BC, .3)

  &:last-child
    margin-right: 0

.action--active
  margin-right: 15px

  transform: scale(1.3)

  color: #000
  border-width: 0
  background: #fff
  box-shadow: 0 1px 20px rgba(#9CA7BC, .3)

.capture-pose
  position: fixed

.pose-select
  position: fixed
  top: 50%
  left: 550px

  display: flex

  transform: translateY(100%) scaleX(0.9)

  .row
    display: flex
    align-items: center
    flex-direction: column

  img
    height: 180px
    margin: 20px

    cursor: pointer
    transition: .3s all ease

    &:hover
      transform: scale(1.1)

#shadow
  position: fixed
  z-index: -1
  top: 77%
  left: 48.5%

  width: 248px
  height: 70px

  opacity: .14
  border-radius: 50%
  background: #08153A

  filter: blur(14px)
